.panel{
    margin-bottom: 0;
    &.panel-default{
        box-shadow: 0 0 5px 5px #eee;
        padding:  0 @padding-mid-h;
        .panel-heading{
            border-bottom: 1px solid @panel-heading-default-border;
            padding: @padding-mid-v 0 @padding-base-v;
            .panel-title{
                color: @title;
            }
            &.no-bd{
                border-bottom: none;
            }
        }
        .panel-body{
            padding:  @padding-base-v 0 @padding-mid-v;
        }
    }
    &.panel-primary{
        .panel-primary{
            .panel-heading{
                background: lighten(@brand-primary,20%);
                color: @title;
                .panel-title{
                    font-size: @font-size-base;
                }
            }
            .panel-body{
                background: lighten(@brand-primary,39%);
            }
        }
    }
    &.no-padding{
       
        >.panel-body{
            padding: 0;
            >.panel{
                border: 0;
            }
        }
    }
     
    &+.panel{
        margin-top: @padding-base-v;
    }
}
.panel-title{
    display: inline-block;
    font-size: @font-size-mid;
    font-weight: 600;
    
}
h4{
    &.panel-title{
        font-size: 16px;
        margin: 0;
    }
}
.panel-default {
    .panel-variant(@panel-default-border; @panel-default-text; @panel-default-heading-bg; @panel-heading-default-border);
}