.table{
   margin-bottom: 0;
   &.table-bordered{
       th{
           background: @grid-header-bg;
       }
       th,
       td{
           &.no-rightbd{
               border-right-color: transparent;
            }
       }
   } 
   &.table-colorfull{
      
      th{
          background: @table-colorful-header-bg;
      } 
      th,td{
          border-color: #fff;
      }
      td{
          background: #fff;
      }
   }
}