.ui-tabs{
    &.under-bd-tabs{
        .ui-tabs-nav{
        @a-height: 30px;
        >li{
            &.ui-tabs-active{
                >a{
                    background: #fff;
                    border: none;
                    border-bottom: 1px solid @brand-primary;
                    color: @brand-primary;
                    padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h;
                    &:hover
                    ,&:focus,&:active{
                        background: #fff;
                        padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h;
                        border: none;
                        border-bottom: 1px solid @brand-primary;
                    }
                }
            }
            >a{
                padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h ;
                border: none;
                border-bottom: 1px solid transparent;
                margin-bottom: -1px;
                min-width: 100px;
                text-align: center;
                font-size: @font-size-mid;
                &:hover,&:focus,&:active{
                    background: #fff;
                    padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h ;
                    border: none;
                }
            }
        }
    }
}
}
.ui-tabs-nav{
    margin-bottom: 0;

}